* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app-wrapper {
  display: grid;
  width: 100%;

  grid-template-areas:
"header header"
"nav content";
 grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: content;
    background-color: burlywood;
}


